import React from 'react';
import { Container } from 'react-bootstrap';
import CardForSpeaker from './CardForSpeaker';
import 'bootstrap/dist/css/bootstrap.min.css';
import kSpeakerList from "../../Assets/KeynoteSpeakers.json";
import tutorialList from "../../Assets/TutorialList.json"

function Talks() {
    return (
        <Container>
            {kSpeakerList.map((speaker) => (
                <CardForSpeaker
                id={speaker.id}
                talk = "Keynote Talk" 
                name = {speaker.name + " (" + speaker.affiliation + ")"}
                title = {speaker.title}
                abstract = {speaker.abstract}            
                bio =  {speaker.bio}            
                />
            ))}
            {tutorialList
            .filter((speeker)=>speeker.should_shown)
            .map((speaker)=>(
                <CardForSpeaker
                id={speaker.id}
                talk= "Tutorial"
                name = {speaker.name + " (" + speaker.affiliation + ")"}
                title = {speaker.title}
                abstract = {speaker.abstract}            
                bio =  {speaker.bio}
                bio2= {speaker.bio2}          
                ></CardForSpeaker>
            ))
            }
        </Container>
        
    );
  }
  
export default Talks;