import React from 'react';
import paperList from '../../Assets/PaperList.json'
import kSpeakerList from "../../Assets/KeynoteSpeakers.json"
import tutorialList from "../../Assets/TutorialList.json"
import { Container } from 'react-bootstrap';
import LinkName from './LinkName';

function TableCellWithLink ({id}) {
  const tech_session_list = Object.values(
          paperList
          .filter((ele)=>ele.paper_type=='tech')
          .reduce((session_info, paper) => {
            // console.log(paper)
            if (!session_info[paper.session_id]){              
              session_info[paper.session_id] = {id: paper.session_id, title: paper.session, papers: []}
            }
            return session_info
          }, {})
        );

  return (
    <Container className='px-0'> 
        {tech_session_list.filter((session)=>session.id==id).map((session)=>(
            <Container className='px-0'>
            <div className='hover-text d-none d-md-inline'>
              <LinkName talk = {"TECH. " +session.title} target= {session.id}/>
            </div>
            <div className='hover-text d-md-none'>
              <LinkName talk = {session.id.toUpperCase()} target= {session.id}/>
            </div>
            </Container>
        ))}
        {kSpeakerList.filter((speaker)=>speaker.id==id).map((speaker)=>(
            <Container className='px-0'>
            <div className='hover-text'><LinkName talk = {speaker.name} target= {speaker.id}/></div>
            </Container>
        ))}
        {tutorialList.filter((speaker)=>speaker.id==id).map((speaker)=>(
            <Container className='px-0'>
            <div className='hover-text'><LinkName talk = {speaker.name} target= {speaker.id}/></div>
            </Container>
        ))}

    </Container>
  );
}

export default TableCellWithLink;