import React from 'react';
import './Card.css'; // Create a separate CSS file for styling
import paperList from '../../Assets/PaperList.json'
import TechSessionCard from './TechSessionCard';
import {Container } from 'react-bootstrap';

function TechSessionList ({}) {
  const session_list = Object.values(
          paperList
          .filter((ele)=>ele.should_shown && ele.paper_type=='tech')
          .reduce((session_info, paper) => {
            // console.log(paper)
            if (!session_info[paper.session_id]){              
              session_info[paper.session_id] = {id: paper.session_id, title: paper.session, papers: []}
            }

            session_info[paper.session_id].papers.push(paper)
            return session_info
          }, {})
        );

  return (
    <Container>
      {session_list.map((session) => (
        <TechSessionCard session_id={session.id} session_title={session.title} papers={session.papers} /> 
      ))}

    </Container>
    
  );
}

export default TechSessionList;
