import React, { useState, useEffect } from "react";

const ScrollToSectionButton = ({ targetId, h2show }) => {

  const [isVisible, setIsVisible] = useState(false);

  // Show button after scrolling down h2show px
  useEffect(() => {
    const handleScroll = () => {
      setIsVisible(window.scrollY > h2show);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const scrollToSection = () => {
    const element = document.getElementById(targetId);
    if (element) {
      element.scrollIntoView({ behavior: "smooth", block: "start" });
    }
  };

  return (
    <button
      onClick={scrollToSection}
      // className={`btn btn-primary rounded-circle position-fixed bottom-0 end-0 m-4 shadow ${
      //   isVisible
      //     ? "opacity-100 translate-y-0"
      //     : "opacity-0 translate-y-8 pointer-events-none"
      // } bg-blue-600 hover:bg-blue-700 text-white`}
      className={`btn btn-success rounded-circle position-fixed bottom-0 end-0 m-4 shadow fade ${
        isVisible ? "opacity-50" : ""
      }`}
      style={{ transition: "opacity 0.3s ease" }}
      aria-label="Scroll to section"
    >
      <i className="bi bi-arrow-up" style={{ fontSize: "1.2rem" }}></i>
    </button>

    // <button
    //   onClick={scrollToSection}
    //   className="fixed bottom-6 right-6 p-3 rounded-full shadow-lg bg-blue-600 hover:bg-blue-700 text-white"
    //   aria-label="Scroll to section"
    // >
    //   <i className="bi bi-info-circle" style={{ fontSize: "1.2rem" }}></i>
    // </button>
  );
};

export default ScrollToSectionButton;
