import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import SimpleCard from "../Common/SimpleCard";
import CommitteeList from "../../Assets/OrgCommitteeList.json";

function OrganizingCommittee(){
    return (
        <Container>
            {/* <br/> */}
            <div className="text-start text-lg-center">
              <h1 style={{ fontSize: "2em" }} className="title">
                <span className="ecu-green"> Organizing  Committee </span>
              </h1>
            </div>

            {/* <h4 style={{fontWeight: 'bold'}}>General Chair</h4> */}
            <Row style={{ justifyContent: "center", paddingBottom: "10px" }}>
                {CommitteeList.filter(ele=>ele.order==0).map((cMember) => (
                    <Col className="col-6 col-sx-6 col-lg-3 col-xl-3 pb-3">
                        <SimpleCard
                        imgPath={cMember.imgPath}
                        name={cMember.name}
                        affiliation={cMember.affiliation}
                        position={cMember.positon} />
                    </Col>              
                ))}
            </Row>

            {/* <h4 style={{fontWeight: 'bold'}}>Program Chairs</h4> */}
            <Row style={{ justifyContent: "center", paddingBottom: "10px" }}>
                {CommitteeList.filter(ele=>ele.order>0).sort(function(ele1, ele2) { return ele1.order < ele2.order? -1 : 1; }).map((cMember) => (
                    <Col className="col-4 col-sx-4 col-lg-3 col-xl-2 pb-2 ps-0 pe-0">
                        <SimpleCard
                        imgPath={cMember.imgPath}
                        name={cMember.name}
                        affiliation={cMember.affiliation}
                        position={cMember.positon} />
                    </Col>              
                ))}
            </Row>
        </Container>

    );
}

export default OrganizingCommittee;