import React from 'react';
import './Card.css'; // Create a separate CSS file for styling

function TechSessionCard ({session_id, session_title, papers}) {
  return (
      
      <div id={session_id} className="card">
        <div className="cardTitle pb-3"> <span className='talkType'>Technical Session</span> : {session_title}</div>
        <div className="text-start">
          {papers.map((paper)=>(
            <div>
              <p className='mb-1'> <span style={{fontWeight: 'bold'}}>{paper.title}</span></p>
              <p className='ps-2' style={{fontSize: '13px'}}> <span style={{fontWeight: 'bold'}}>Authors: </span>{paper.authors}</p>
            </div>
          ))}
        </div>
      </div>
  );
}

export default TechSessionCard;
